@import "minima";

ul {
  list-style:none;
}

.exemple {
  font-style: italic;
  text-align: center;
}

.nav-link {
  color: #fee21f !important;
}

nav {
  background-color: #0f3696 !important;
}

h2, h1, p, i {
  color: rgba(255, 255, 255, .85);;
}

.page-content {
  padding-bottom: 200px;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: 'Kdam Thmor Pro', sans-serif;
}

:root {
  --clr: #222327;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: var(--clr);
  max-width: 100%;
}

.bottom-nav {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 95%;
  height: 70px;
  background: #fff;
  display: flex;
  justify-content: left;
  align-items: left;
  border-radius: 10px;
  margin: 15px 10px;
}

.bottom-bg {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100vh;
  height: 130px;
  background: var(--clr);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
}

.bottom-nav ul {
  display: flex;
  width: 350px;
  margin-left: 10px !important;
}

.bottom-nav ul li {
  list-style: none;
  width: 70px;
  height: 70px;
  z-index: 1;
}

.bottom-nav ul li a {
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  text-align: center;
  font-weight: 500;
}

.link-story {
  color: #56aa95;
  background: #222327;
}

.bottom-nav ul li a .icon-nav {
  position: relative;
  display: block;
  line-height: 75px;
  font-size: 1.5em;
  text-align: center;
  transition: 0.5s;
  color: var(--clr);
}

.bottom-nav ul li.nav-active a .icon-nav {
  transform: translateY(-32px);
}

.bottom-nav ul li a .text-nav {
  position: absolute;
  color: var(--clr);
  font-weight: 400;
  font-size: 0.75em;
  letter-spacing: 0.05em;
  transition: 0.5s;
  opacity: 0;
  transform: translateY(20px);
}

.bottom-nav ul li.nav-active a .text-nav {
  opacity: 1;
  transform: translateY(10px);
}

.nav-indicator {
  position: absolute;
  top: -50%;
  width: 70px;
  height: 70px;
  background: #29fd53;
  border-radius: 50%;
  border: 6px solid var(--clr);
  transition: 0.5s;
}

.nav-indicator::before {
  content: '';
  position: absolute;
  top: 50%;
  left: -22px;
  width: 20px;
  height: 20px;
  background: transparent;
  border-top-right-radius: 20px;
  box-shadow: 1px -10px 0 0 var(--clr);
}

.nav-indicator::after {
  content: '';
  position: absolute;
  top: 50%;
  right: -22px;
  width: 20px;
  height: 20px;
  background: transparent;
  border-top-left-radius: 20px;
  box-shadow: -1px -10px 0 0 var(--clr);
}

.bottom-nav ul li:nth-child(1).nav-active ~ .nav-indicator {
  transform: translateX(calc(70px * 0));
}

.bottom-nav ul li:nth-child(2).nav-active ~ .nav-indicator {
  transform: translateX(calc(70px * 1));
}

.bottom-nav ul li:nth-child(3).nav-active ~ .nav-indicator {
  transform: translateX(calc(70px * 2));
}

.bottom-nav ul li:nth-child(4).nav-active ~ .nav-indicator {
  transform: translateX(calc(70px * 3));
}

.bottom-nav ul li:nth-child(5).nav-active ~ .nav-indicator {
  transform: translateX(calc(70px * 4));
}

// Header

img {
  pointer-events: none;
}

.story-container ul {
  display: flex;
  padding: 20px 10px;
  justify-content: center;
}

.story-container ul li {
  padding: 0px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.story-container ul li:first-child {
  padding-left: 0px;
}

.story-container ul li:last-child {
  padding-right: 40px;
}

.story-container ul li .story {
  width: 71px;
  height: 71px;
  border-radius: 50%;
  background: rgba(255, 255, 255, .75);
  padding: 1px;
  position: relative;
}

.story-container ul li.has-story .story {
  padding: 2px;
  background: linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
}

.story-container ul li .story img {
  padding: 4px;
  background: currentColor;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  position: relative;
}

.story-container ul li span {
  color: rgba(255, 255, 255, .85) !important;
  font-weight: 300;
  font-size: 15px;
  max-width: 85px;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-top: 5px;
}

.story-container {
  margin: 0px;
  padding: 0px;
  width: 100vh;
}

// CatÃ©gories :

.box-categories-container {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
}

.box-categories-container .categories-card {
  width: 40%;
  height: 300px;
  background: grey;
  margin: 10px;
  border-radius: 20px;
}

.box-categories-container .categories-card-title {
  text-align: center;
}

.box-categories-container .categories-card-img {
  display: block;
  margin: 30% auto;

}

.box-categories-container .categories-card .categories-card-div-button {
  background-color: var(--clr);
  width: 75%;
  height: 20%;
  display: block;
  margin: 30% auto;
  border-radius: 45%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.box-categories-container .categories-card span {
  color: #fff;
}
